import _ from 'lodash';
import React, { Component } from 'react';
import { Col, Row } from 'reactstrap';
import PropTypes from 'prop-types';
import { LazyLoadImage } from 'react-lazy-load-image-component';

import ReactSlickCarousel from 'components/Templates/ReactSlickCarousel/ReactSlickCarousel';

// import 'react-lazy-load-image-component/src/effects/opacity.css';
import './LandingBlog.scss';

class LandingBlog extends Component {
  constructor(props) {
    super(props);

    this.state = {
      index: 0,
    };
  }

  componentDidMount() {
    const { getBlogData } = this.props;

    // getBlogData();
  }

  renderBlogImage() {
    const { blogData } = this.props;

    if (!blogData) {
      return null;
    }

    try {
      return (
        <div className="blog-carousel-container">
          <ReactSlickCarousel
            lazyLoad
            autoplay
            autoplaySpeed={5000}
            arrows
            slidesToShow={1}
            className="white-sliders"
            beforeChange={(oldIndex, newIndex) => this.setState({ index: newIndex })}
            responsive={[{
              breakpoint: 992,
              settings: {
                dots: true,
                arrows: false,
                className: 'green-dots',
              },
            }]}
          >
            {
              _.map(blogData, (data, index) => (
                <LazyLoadImage
                  key={`blog-img-${index}`}
                  className="blog-img"
                  src={data.img}
                  alt={`blog-img-${index}`}
                />
              ))}
          </ReactSlickCarousel>
        </div>
      );
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }

  renderBlogInfo() {
    const { index } = this.state;
    const { blogData } = this.props;

    const data = blogData && blogData[index];
    if (!data) {
      return null;
    }

    try {
      return (
        <div className="blog-info-container">
          <h3>
            <a
              href={data.link}
              target="__blank"
              dangerouslySetInnerHTML={{ __html: data.title || '&nbsp;' }}
            />
          </h3>
          <p className="date">{data.date}</p>
          <p className="info" dangerouslySetInnerHTML={{ __html: data.excerpt || '&nbsp;' }} />
          <a
            className="read-more"
            href={data.link}
            target="__blank"
          >
            Read more
          </a>
        </div>
      );
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }

  render() {
    return (
      <Row className="landing-blog-container" noGutters>
        <Col xs={12}>
          <h2>Blog</h2>
        </Col>
        <Col xs={12} lg={6}>
          {this.renderBlogImage()}
        </Col>
        <Col className="my-auto" xs={12} lg={6}>
          {this.renderBlogInfo()}
        </Col>
      </Row>
    );
  }
}

LandingBlog.defaultProps = {
  blogData: null,
};

LandingBlog.propTypes = {
  blogData: PropTypes.array,
  getBlogData: PropTypes.func.isRequired,
};

export default LandingBlog;
