import { connect } from 'react-redux';
import LandingBlog from 'components/Landing/LandingBlog/LandingBlog';

import { fetchBlogFeed } from 'actions/blogActions';

// eslint-disable-next-line
const mapStateToProps = state => ({
  blogData: state.blog.blogData,
});

const mapDispatchToProps = dispatch => ({
  getBlogData: () => {
    dispatch(fetchBlogFeed());
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(LandingBlog);
